import { Component, h, Prop } from '@stencil/core';
import { IRecipient } from '../../types/Recipient';

@Component({
    tag: 'share-document-link',
    shadow: true,
    styleUrl: 'share-document-link.scss',
})
export class ShareDocumentLink {
    @Prop() public recipient: IRecipient;

    constructor() {
        this.handleCopyLink = this.handleCopyLink.bind(this);
    }

    public render() {
        return (
            <li class="share-document-list-item">
                <div class="recipient-info-container">
                    <div class="recipient-icon">
                        <limel-icon name="user" size="small" />
                    </div>
                    <div class="recipient-info">
                        <span class="recipient-info-name">
                            {this.recipient.fullname}
                        </span>
                        <span class="recipient-info-role">
                            {this.recipient.role}
                        </span>
                    </div>
                </div>
                <div>
                    <limel-input-field
                        label="Signing link"
                        type="email"
                        value={this.recipient.document_url}
                        trailingIcon="copy_link"
                        onAction={this.handleCopyLink}
                    />
                </div>
            </li>
        );
    }

    handleCopyLink() {
        const copyText = document.createElement('textarea');
        copyText.value = this.recipient.document_url;
        document.body.appendChild(copyText);
        copyText.select();
        document.execCommand('copy');
        document.body.removeChild(copyText);
    }
}
